// Hovers

@media (min-width: 768px) {

	.btn-1:hover {
		transform: scale(1.125);
		box-shadow: 0px 7px 20px rgba(157, 255, 250, 0.4);
		color: #3D48A9;
	}

	.btn-1:active {
		transform: scale(1);
		box-shadow: none;
	}

	.btn-1:active:after {
		opacity: 1;
	}

	.info-tmb-arrow {
		transition: all .2s ease;
		overflow: hidden;
	}

	.link-arrow {
		transition: all .2s ease;
		overflow: hidden;
	}

	.info-tmb:hover .info-tmb-arrow,
	.link:hover .link-arrow {
		transform: scale(1.125);
		box-shadow: 0px 7px 20px rgba(157, 255, 250, 0.4);
		color: #3D48A9;
	}

	.info-tmb .info-tmb-arrow:before,
	.link-arrow:before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0,0,0,.2);
		opacity: 0;
		transition: all .2s ease;
	}

	.info-tmb:active .info-tmb-arrow:before,
	.link:active .link-arrow:before {
		opacity: 1;
	}

	.info-tmb:active .info-tmb-arrow,
	.link:active .link-arrow {
		transform: scale(1);
		box-shadow: none;
	}

	.soclink {
		transition: all .2s ease;
		overflow: hidden;
		position: relative;
	}

	.soclink:hover {
		transform: scale(1.125);
		box-shadow: 0px 7px 20px rgba(157, 255, 250, 0.4);
	}

	.soclink:active {
		transform: scale(1);
		box-shadow: none;
	}

	.soclink:before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: rgba(0,0,0,.2);
		opacity: 0;
		transition: all .2s ease;
	}

	.soclink:active:before {
		opacity: 1;
	}

	.play-button {
		overflow: hidden;
	}

	.play-button:before {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: radial-gradient(at 113.54% 94.76%, #CAFFF5 0%, #A0FFFF 44.27%, #205EFF 100%);
		opacity: 0;
		transition: all .2s ease;
	}

	.video-poster:hover .play-button:before {
		opacity: 1;
	}

	.video-poster:active .play-button:before {
		opacity: 0;
	}
	
	.video-poster:active .play-button {
		background: #b7b7b7;
	}

	.footer-soclink img {
		position: relative;
		z-index: 10;
	}

	.footer-soclink {
		transition: all .2s ease;
		overflow: hidden;
		position: relative;
	}

	.footer-soclink:before {
		content: "";
		background: radial-gradient(at 113.54% 94.76%, #CAFFF5 0%, #A0FFFF 44.27%, #205EFF 100%);
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		opacity: 0;
		transition: all .2s ease;
	}

	.footer-soclink:hover:before {
		opacity: 1;
	}

	.footer-soclink:hover {
		transform: scale(1.125);
		box-shadow: 0px 7px 20px rgba(157, 255, 250, 0.4);
	}

	.footer-soclink:after {
		content: "";
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		bottom: 0;
		background: #b7b7b7;
		opacity: 0;
		transition: all .2s ease;
	}

	.footer-soclink:active:before {
		opacity: 0;
	}

	.footer-soclink:active:after {
		opacity: 1;
	}

	.footer-soclink:active {
		box-shadow: none;
		transform: scale(1);
	}

	ul.navbar-nav a:hover {
		background: radial-gradient(at 113.54% 94.76%, #CAFFF5 0%, #A0FFFF 44.27%, #205EFF 100%);
		-webkit-background-clip: text;
		-webkit-text-fill-color: transparent;
	}

}


