@media (max-width: 1109px) {

	#md-indicator {
		display: block;
	}

	html,
	body {
		font-size: 12px;
		line-height: 1.67;
	}

	.row {
		margin: 0 -5px;
	}

	.col {
		padding: 0 5px;
	}

	.header-logo-1 img {
		width: 88px;
	}

	.header-logo-1 {
		margin: 0 34px 0 0;
	}

	.header-logos {
		margin: 0 -34px 0 0;

	}

	.header-logo-2 {
		margin: 0 34px 0 0;

	}

	.header-logo-2 img {
		width: 71px;
	}

	.navbar-wrapper-inner {
		padding: 8px 0 45px 0;
	}

	.navbar-wrapper-inner .cont {
		margin: 0 auto;
		max-width: 610px;
	}

	.navbar-button {
		display: block;
		margin: 41px 0 0 0;
	}

	.navbar-button .btn {
		width: 100%;
	}

	ul.navbar-nav {
		font-size: 16px;
		margin: 0;
		display: block;
		font-weight: normal;
	}

	ul.navbar-nav > li {
		margin: 0;
	}

	ul.navbar-nav a,
	ul.navbar-nav a:focus {
		display: block;
		position: relative;
		border-bottom: 1px solid rgba(255,255,255,.15);
		color: #fff !important;
		padding: 20px 0 19px 0;
	}

	ul.navbar-nav a:after {
		content: "";
		background: url(../images/navbar-arrow.svg) 0 0 no-repeat;
		width: 20px;
		height: 20px;
		position: absolute;
		right: 0;
		top: 50%;
		margin-top: -10px;
	}

	.header-content {
		padding: 14px 0;
	}

	.btn-1, .btn-1:focus {
		font-size: 12px;
		border-radius: 12px;
		padding: 8px 24px 7px 24px;
	}
	
	.navbar-trigger {
		display: block;
		width: 25px;
		height: 25px;
		position: absolute;
		right: 5px;
		top: 50%;
		margin-top: -12px;
		cursor: pointer;
	}
	
	.navbar-trigger span {
		transition: all .3s ease;
		position: absolute;
		left: 0px;
		right: 0px;
		height: 2px;
		background: #fff;
	}

	.navbar-trigger span:nth-child(1) {
		top: 6px;
	}

	.navbar-trigger span:nth-child(2) {
		bottom: 7px;
	}

	.navbar-trigger.active span:nth-child(1) {
		top: 12px;
		transform: rotate(45deg);
	}

	.navbar-trigger.active span:nth-child(2) {
		bottom: 11px;
		transform: rotate(-45deg);
	}
	

	.navbar-wrapper {
		display: none;
		position: fixed;
		left: 0;
		right: 0;
		top: 50px;
