@media (max-width: 767px) {

	.anim,
	.section-top-pic {
		opacity: 1 !important;
		transform: none !important;
	}

}

.section-top .anim:not(img) {
	opacity: 0;
	transform: translateY(50px);
}



.section-top-pic {
	opacity: 0;
	transform: scale(.75);
}




.info-tmb {
	opacity: 0;
	transform: translateY(50px);
}

.stage-tmb {
	opacity: 0;
	transform: translateY(50px);
}

.criteria-tmb {
	opacity: 0;
	transform: scale(.85);
}

.prize-tmb-num,
.links-list .anim,
.prize-tmb-text {
	opacity: 0;
	transform: translateY(50px);
}

.prize-tmb-pic {
	opacity: 0;
	transform: translateX(100px);
}